	module.exports = {"data":{"markdownRemark":{"html":"<p>Think about the relationship between the central and local, write on paper:</p>\n<p>The authority of the central government should be written from the authority of the whole party. In the reform and opening up, the whole party must have authority. The concentrated representative of this authority is the central government;</p>\n<p>The central and local party organizations must have authority, but the key is the central government, the central government has authority, and the local authorities have authority;</p>\n<p>Maintaining the authority of the central government is to maintain the authority of the whole;</p>\n<p>If there is no existing decision in the central government involved, the central government must be requested;</p>\n<p>The decision of major local issues is notified to the central government in a timely manner;</p>\n<p>The locality is motivated and creative under the unified leadership of the central government;</p>\n<p>The central government actively supports local work;</p>\n<p>The local party committee has the responsibility to ensure that the central government order is unimpeded in the region;</p>\n<p>The essence of central authority is the unified leadership on the basis of the united will of the whole party;</p>\n<p>The correct decision can guarantee efficiency;</p>\n<p>Those belonging to government management should be resolved through democratic centralism, and those that do not belong should be resolved through market mechanisms.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0103.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0103.md","frontmatter":{"title":"Wednesday, June 1"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0103.md absPath of file >>> MarkdownRemark"}}

